<template>
	<view class="diy_goods">
		<view class="row">
			<view v-for="(o, i) in list" :key="i" :class="'col-' + col">
				<navigator :url="'/pages/goods/details?goods_id=' + o[vm.goods_id]" class="goods">
					<image style="width: 100%; height: auto;" src="../../static/logo.png" mode="widthFix"></image>
					<view><text>{{ o[vm.title] }}</text></view>
					<view><text class="price">￥{{ o[vm.price] }}</text><text class="price_ago">￥{{ o[vm.price_ago] }}</text></view>
				</navigator>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						goods_id: "goods_id",
						img: "img",
						title: "title",
						price: "price",
						price_ago: "price_ago"
					}
				}
			},
			span: {
				type: Number,
				default: 2
			}
		},
		data() {
			var col = 12 / this.span;
			return {
				col
			}
		}
	}
</script>

<style scoped>
	.row {
		justify-content: start;
	}

	.goods {
		padding: .75rem;
	}

	.price {
		color: var(--color_orange);
		font-weight: 600;
	}

	.price_ago {
		margin-left: .5rem;
		text-decoration: line-through;
		font-size: 0.875rem;
		color: #999;
	}
</style>
